            }
        }
        &--line {
            display: flex;
            align-items: center;
            margin-bottom: 8px;
            padding-bottom: 10px;
            border-bottom: 1px solid #E5E5E5;
            &:last-child {
                border-bottom: none;
            }
        }
        &--p {
            font-size: 14px;
            line-height: 120%;
            color: #000;
        }
        &--calcin {
            display: flex;
            align-items: center;
            width: 120px;
            height: 20px;
            margin-left: 20px;
            .numb {
                width: 50px;
                height: 21px;
                background: #FFFFFF;
                box-shadow: inset 0px 0px 4.25806px rgba(0, 0, 0, 0.15);
                font-size: 14px;
                color: #FFB100;
                text-align: center;
            }
            .plus {
                background: #FFB100;
                border-radius: 3px;
                color: #fff;
                width: 100%;
                text-align: center;
                font-size: 18px;
                .m();
                cursor: pointer;
            }
            .minus {
                background: #FFB100;
                border-radius: 3px;
                color: #fff;
                width: 100%;
                text-align: center;
                font-size: 18px;
                .m();
                cursor: pointer;
            }
        }
        &--red {
            margin-left: 10px;
            font-size: 20px;
            line-height: 120%;
            color:  #FFB100;
            .m();
        }
    }
    &_form {
        max-width: 295px;
        .xs-block({
            margin-top: 25px;
            max-width: inherit;
        });
        &--title {
            font-size: 20px;
            color: #000;
        }
        &--input {
            width: 100%;
            height: 40px;
            background: #FAFAFA;
            border: 2px solid #E5E5E5;
            border-radius: 5px;
            padding: 0 15px;
            margin-top: 10px;
            &:first-child {
                margin-top: 0;
            }
            &::placeholder {
                font-size: 13px;
                color: #C4C4C4;
            }
        }
        &--textarea {
            width: 100%;
            height: 40px;
            background: #FAFAFA;
            border: 2px solid #E5E5E5;
            border-radius: 5px;
            padding: 10px 15px;
            margin-top: 10px;
            &::placeholder {
                font-size: 13px;
                color: #C4C4C4;
            }
        }
        &--btn {
            cursor: pointer;
            margin-top: 15px;
            .btn();
            height: 40px;
            line-height: 38px;
            width: 100%;
        }
    }
}